<template>
 <div class="wholeRouter">
    <div class="button-group">
      <span class="route-name">{{$route.meta.title}}</span>
      <el-button icon="el-icon-plus"  size="small" @click="routingAdd()" type="warning">{{$t('common.add')}}</el-button>
    </div>
    <el-form :model="serviceForm" ref="serviceForm" label-width="94px" label-position="right" size="small"  :rules="inputRules">
      <el-row class="label-first" :gutter="20">
        <el-col :xl="4" :lg="6">
          <el-form-item :label="$t('redis.redisCluster.clusterName')+'：'" prop="zkClusterId">
            <el-select v-model="serviceForm.zkClusterId" clearable :placeholder="$t('common.pleaseChoose')" style="width:100%">
              <el-option
                v-for="(item) in clusterOption"
                :key="item.id"
                :label="item.pkDesc"
                :value="item.pkValue">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :xl="4" :lg="6">
          <el-button size="small" type="primary" icon="el-icon-search" @click="search()">{{$t('common.search')}}</el-button>
        </el-col>
      </el-row>
    </el-form>
    <!-- 表格部分 -->
    <el-table
      stripe
      :data="routintList"
      v-loading="loading"
      ref="multipleTable"
      style="width: 100%;">
          <el-table-column
            align="center"
            prop="type"
            :label="$t('common.type')">
          </el-table-column>
          <el-table-column
            align="center"
            prop="condition"
            :label="$t('common.condition')">
          </el-table-column>
          <el-table-column
            width="80"
            :label="$t('common.operation')"
            fixed="right"
            align="center">
            <template slot-scope="props">
              <el-button type="text" size="mini" icon="fa fa-pencil-square-o" @click="routingDelete(props)">删除</el-button>
            </template>
          </el-table-column>
    </el-table>
    <!-- 翻页部分 -->
    <div class="block">
      <el-pagination
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-size="10"
        layout="total, prev, pager, next, jumper"
        background
        :total="totalPage">
      </el-pagination>
    </div>
    <!-- 弹出框 -->
    <el-dialog :title="'添加全局路由'" @close="cancel()" :visible.sync="popBox" width="40%" center>
      <el-form ref="popForm" :model="popForm" label-width="120px" size="small" status-icon :rules="inputRules">
        <el-row>
          <el-col :span="24">
            <el-form-item :label="$t('redis.redisCluster.clusterName')+'：'" prop="zkClusterId">
              <el-select v-model="popForm.zkClusterId" clearable :placeholder="$t('common.pleaseChoose')" style="width:100%">
                <el-option
                  v-for="(item) in clusterOption"
                  :key="item.id"
                  :label="item.pkDesc"
                  :value="item.pkValue">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-form-item :label="$t('common.type')+'：'" prop="type">
              <el-input type="text" v-model.trim="popForm.type" placeholder="请输入1-50位字符" maxlength="50" clearable></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-form-item :label="$t('common.condition')+'：'" prop="condition">
              <el-input type="text" v-model.trim="popForm.condition" placeholder="请输入1-50位字符" maxlength="50" clearable></el-input>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button :loading="loading" @click="popOk()" type="primary" size="small">{{$t('common.ok')}}</el-button>
        <el-button @click="cancel()" size="small">{{$t('common.cancel')}}</el-button>
      </div>
    </el-dialog>
 </div>
</template>

<script type="text/ecmascript-6">
import Api from '@/constants/Api.js'
import {mapState, mapActions} from 'vuex'
export default {
    name: 'wholeRouter',
    data() {
        return {
            currentPage: 1, // 当前页数
            totalPage: 0, // 总页数
            loading: false,
            // 查询/弹出框-规则
            inputRules: {
                zkClusterId: [
                    { required: true, message: '请输入集群名称', trigger: 'blur' }
                ],
                type: [
                    { required: true, message: '请输入类型', trigger: 'blur' }
                ],
                condition: [
                    { required: true, message: '请输入条件', trigger: 'blur' }
                ]
            },
            // 查询模板
            serviceForm: {},
            // 全部数据
            dataBox: [],
           // table数据
            routintList: [],
            // 弹出框
            popBox: false,
            // 弹出框模板
            popForm: {}
        }
    },
    computed: {
      ...mapState({
        clusterOption: state => state.service.clusterOption
      })
    },
    activated() {
      if (this.clusterOption.length <= 0) {
        const vm = this
        this.initializeData({vm})
      }
    },
    methods: {
      ...mapActions('service', ['initializeData']),
      // 翻页事件
      handleCurrentChange(val) {
        this.currentPage = val
        this.routintList = this.dataBox[val - 1]
      },
      // 查询
      search() {
        const query = Object.assign({}, this.serviceForm)
        let {zkClusterId} = query
        if (!zkClusterId) {
          this.$message({
            message: '值不能为空',
            type: 'warning'
          })
          return false
        }
        this.loading = true
        this.$axios({
          method: 'get',
          url: Api.serviceSFRouter,
          params: {
            zkClusterId
          }
        }).then((res) => {
          res.data.forEach((value) => {
            if (!value.zkClusterId) {
              value.zkClusterIdBackup = this.serviceForm.zkClusterId
            }
          })
          let data = JSON.parse(JSON.stringify(res.data))
          this.totalPage = data.length
          this.dataBox = this.conmmonMethods.arrayHandle(data)
          this.routintList = this.dataBox[this.currentPage - 1]
          this.loading = false
          // this.$message({
          //   message: '查询成功',
          //   type: 'success'
          // })
        }).catch((error) => {
          this.loading = false
          this.totalPage = 0
          this.dataBox = []
          this.currentPage = 1
          this.routintList = []
          this.$message.error(error.data.message || error.message || error)
        })
      },
      // 新增按钮
      routingAdd() {
        this.popForm = {}
        this.popBox = true
      },
      // 新增确定
      popOk() {
        const query = Object.assign({}, this.popForm)
        let {zkClusterId, type, condition} = query
        if (!zkClusterId || !type || !condition) {
          this.$message({
            showClose: true,
            message: '带*不能为空',
            type: 'warning'
          })
          return
        }
        this.loading = true
        this.$axios({
          method: 'post',
          url: Api.serviceSFAdd,
          params: {
            'zkClusterId': zkClusterId
          },
          data: {
            type, condition
          }
        }).then((res) => {
          this.popBox = false
          this.$message({
            message: '新增成功',
            type: 'success'
          })
          this.serviceForm.zkClusterId = query.zkClusterId
          this.search()
        }).catch((error) => {
          this.loading = false
          this.$message.error(error.data.message || error.message || error)
        })
      },
      cancel() {
        this.$refs.popForm.resetFields()
        this.popBox = false
      },
      // 删除按钮
      routingDelete(props) {
        let query = Object.assign({}, props.row)
          if (!query.zkClusterId) {
            query.zkClusterId = props.row.zkClusterIdBackup
          }
        let data = {
            type: query.type,
            condition: query.condition
          }
        this.conmmonMethods.makePopBox(this, '删除').then(() => {
          this.loading = true
          this.$axios({
            method: 'post',
            url: Api.serviceSFDelete,
            params: {
              'zkClusterId': query.zkClusterId
            },
            data: data
          }).then((res) => {
            this.$message({
              message: '删除成功',
              type: 'success'
            })
            this.search()
          }).catch((error) => {
            this.loading = false
            this.$message.error(error.data.message || error.message || error)
          })
        })
      }
    }
}
</script>

<style scoped lang="scss">
</style>



